import React from "react";
import { Lock, Sms, User, Calendar, ArrowSwapVertical } from "iconic-react";
import styled from "styled-components";

const InputContainer = styled.div`
  display: flex;
  align-items: center;
  border: 0px;
  border-radius: 5px;
  width: 100%; //   .
  min-width: 340px;
  height: 48px;
  background-color: #f7f8f8;
  margin-bottom: 7px;
`;

const Input = styled.input`
  border: none;
  outline: none;
  display: flex;
  width: 100%;
  padding: 5px;
  background: #f7f8f8;
`;

// Iconic     

const IconUser = styled(User)`
  margin-right: 5px;
  margin-left: 10px;
`;

const IconSms = styled(Sms)`
  margin-right: 5px;
  margin-left: 10px;
`;

const IconLock = styled(Lock)`
  margin-right: 5px;
  margin-left: 10px;
`;

const IconCalendar = styled(Calendar)`
  margin-right: 5px;
  margin-left: 10px;
`;

const IconSwap = styled(ArrowSwapVertical)`
  margin-right: 5px;
  margin-left: 10px;
`;

const Text = styled.span`
  margin-right: 13px; /*      . */
  font-size: 14px; /*   . */
`;

const IconInput = ({ icontype, placeholder, rightSpan }) => {
  return (
    <InputContainer>
      <SelectedIcon icontype={icontype}></SelectedIcon>
      <Input type="text" placeholder={placeholder} />
      {rightSpan && <Text>{rightSpan}</Text>}
    </InputContainer>
  );
};

export const SelectedIcon = ({ icontype }) => {
  console.log(icontype);
  if (icontype === "user") {
    return (
      <IconUser size={20} color="#000">
        
      </IconUser>
    );
  } else if (icontype === "sms") {
    return (
      <IconSms size={20} color="#000">
        
      </IconSms>
    );
  } else if (icontype === "lock") {
    return (
      <IconLock size={20} color="#000">
        
      </IconLock>
    );
  } else if (icontype === "Calendar") {
    return (
      <IconCalendar size={20} color="#000">
        
      </IconCalendar>
    );
  } else if (icontype === "ArrowSwapVertical") {
    return (
      <IconSwap size={20} color="#000">
        
      </IconSwap>
    );
  } else {
    return null;
  }
};

export default IconInput;
